* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

body {
  background-color: hsl(210deg, 46%, 95%);
}

a {
  display: block;
}

.icon {
  width: 1.25rem;
  object-fit: contain;
}

@media (min-width: 768px) {
  .icon {
    width: 1.125rem;
  }
}
.main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Manrope", sans-serif;
  color: hsl(214deg, 17%, 51%);
}

.card {
  width: 90%;
  max-width: 700px;
  background-color: #fff;
  border-radius: 0.625rem;
  position: relative;
  overflow: hidden;
}
.card__heading {
  font-weight: 700;
  color: hsl(217deg, 19%, 35%);
  font-size: 15px;
}
.card__text {
  margin-top: 0.9375rem;
  font-size: 13px;
  line-height: 1.5;
}

.wrapper__text {
  padding-block: 25px 0;
  padding-inline: 40px;
}

.author {
  display: flex;
}
.author__avatar {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50px;
  margin-right: 20px;
}
.author__img {
  border-radius: inherit;
}
.author__name {
  color: hsl(217deg, 19%, 35%);
  font-size: 0.75rem;
  font-weight: 700;
}
.author__date {
  font-size: 0.75rem;
  color: hsl(214deg, 17%, 51%);
  font-weight: 500;
}

.share {
  display: none;
}
.share__text {
  color: hsl(212deg, 23%, 69%);
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
  margin-right: 1.25rem;
  font-size: 0.875rem;
  font-weight: 500;
}
.share__icons {
  display: grid;
  grid-auto-flow: column;
  gap: 14px;
}

.btn {
  width: 1.875rem;
  color: #6E8098;
  background-color: hsl(210deg, 46%, 95%);
  border-radius: 3.125rem;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  cursor: pointer;
  z-index: 1;
}

.footer {
  display: flex;
  align-items: center;
  padding: 1.25rem 2.5rem;
  height: 4.375rem;
  margin-top: 0.625rem;
  position: relative;
}
.footer::before {
  content: "";
  position: absolute;
  bottom: -100%;
  right: 0;
  left: 0;
  height: 100%;
  background-color: hsl(217deg, 19%, 35%);
  z-index: 0;
  transition: bottom 0.3s linear;
}

.share {
  animation: fadeIn 3s ease;
  animation-direction: alternate;
}

.checkbox {
  display: none;
}
.checkbox:checked + .footer::before {
  bottom: 0;
}
.checkbox:checked + .footer > .btn {
  background-color: hsl(214deg, 17%, 51%);
  color: #fff;
}
.checkbox:checked + .footer > .author {
  display: none;
}
.checkbox:checked + .footer > .share {
  display: flex;
  place-items: center;
  z-index: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scaleY(0);
  }
  30% {
    opacity: 1;
    transform: scaleY(1);
  }
}
@media (min-width: 768px) {
  .card {
    display: grid;
    grid-template-columns: 1.5fr 2fr;
    overflow: visible;
  }
  .card__image--img {
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
  }
  .card__heading {
    font-size: 1.125rem;
  }
  .card__text {
    font-size: 0.75rem;
  }
  .wrapper__text {
    padding-top: 1.875rem;
    padding-inline: 2.5rem;
  }
  .share {
    display: flex;
    opacity: 0;
    transform: scaleY(0);
    transition: opacity 0.3s linear, transform 0.3s linear;
    animation: none;
    background: hsl(217deg, 19%, 35%);
    border-radius: 0.4375rem;
    left: calc(100% - 175px);
    padding: 0.8125rem 1.5rem;
    position: absolute;
    top: -3.125rem;
    box-shadow: 1px 5px 14px #94a1b7;
  }
  .share::after {
    background-color: hsl(217deg, 19%, 35%);
    bottom: -12%;
    content: "";
    height: 1.0625rem;
    position: absolute;
    transform: rotate(46deg);
    width: 1.0625rem;
    left: calc(100% - 112.5px - 10px);
  }
  .share__avatar {
    margin-right: 1.625rem;
  }
  .share__text {
    font-size: 0.75rem;
    letter-spacing: 0.3125rem;
    text-transform: uppercase;
    margin-left: 0.625rem;
  }
  .share__icons {
    gap: 0.9375rem;
    margin-right: 0.625rem;
  }
  .footer {
    position: relative;
    margin: 0;
    padding-block: 2.5rem 3.125rem;
  }
  .footer::before {
    display: none;
  }
  .checkbox:checked + .footer {
    background-color: unset;
    animation: unset;
  }
  .checkbox:checked + .footer > .author {
    display: flex;
  }
  .checkbox:checked + .footer > .share {
    display: flex;
    place-items: center;
    opacity: 1;
    transform: scaleY(1);
  }
}