@use 'variables' as v;
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
body{
    background-color: v.$bgcard;
}
a{
    display: block;
}
.icon{
    width: 1.25rem;
    object-fit: contain;
}
@media (min-width:768px) {
    .icon{
        width: 1.125rem;
    }
}